.van-rate {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-user-select: none;
          user-select: none;
}
.van-rate__item {
  position: relative;
  padding: 0 0.08533rem;
  padding: 0 var(--rate-horizontal-padding, 0.08533rem);
}
.van-rate__item:not(:last-child) {
  padding-right: 0.17067rem;
  padding-right: var(--rate-icon-gutter, 0.17067rem);
}
.van-rate__icon {
  display: block;
  height: 1em;
  color: #c8c9cc;
  color: var(--rate-icon-void-color, #c8c9cc);
  font-size: 0.85333rem;
  font-size: var(--rate-icon-size, 0.85333rem);
}
.van-rate__icon--half {
  position: absolute;
  top: 0;
  width: 0.5em;
  overflow: hidden;
  left: 0.08533rem;
  left: var(--rate-horizontal-padding, 0.08533rem);
  color: #ee0a24;
  color: var(--rate-icon-full-color, #ee0a24);
}
.van-rate__icon--full {
  color: #ee0a24;
  color: var(--rate-icon-full-color, #ee0a24);
}
.van-rate__icon--disabled {
  color: #c8c9cc;
  color: var(--rate-icon-disabled-color, #c8c9cc);
}
/* stylelint-disable selector-type-no-unknown, selector-max-type */
/**
 * Entry of basic styles
 */
.van-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.van-multi-ellipsis--l2 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-multi-ellipsis--l3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.van-hairline,
.van-hairline--top,
.van-hairline--left,
.van-hairline--right,
.van-hairline--bottom,
.van-hairline--top-bottom,
.van-hairline--surround {
  position: relative;
}
.van-hairline::after,
.van-hairline--top::after,
.van-hairline--left::after,
.van-hairline--right::after,
.van-hairline--bottom::after,
.van-hairline--top-bottom::after,
.van-hairline--surround::after {
  position: absolute;
  box-sizing: border-box;
  -webkit-transform-origin: center;
          transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.van-hairline--top::after {
  /* prettier-ignore */
  border-top-width: 1PX;
}
.van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX;
}
.van-hairline--right::after {
  /* prettier-ignore */
  border-right-width: 1PX;
}
.van-hairline--bottom::after {
  /* prettier-ignore */
  border-bottom-width: 1PX;
}
.van-hairline--top-bottom::after {
  /* prettier-ignore */
  border-width: 1PX 0;
}
.van-hairline--surround::after {
  /* prettier-ignore */
  border-width: 1PX;
}
html,
body,
page {
  font-size: 0.59733rem;
}
