.van-radio {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden;
  -webkit-user-select: none;
          user-select: none;
}
.van-radio__icon-wrap {
  -webkit-flex: none;
          flex: none;
}
.van-radio--horizontal {
  margin-right: 0.512rem;
  margin-right: var(--padding-sm, 0.512rem);
}
.van-radio__icon {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  box-sizing: border-box;
  width: 1em;
  height: 1em;
  color: transparent;
  text-align: center;
  -webkit-transition-property: color, border-color, background-color;
  transition-property: color, border-color, background-color;
  border: 0.04267rem solid #c8c9cc;
  border: var(--border, 0.04267rem) solid var(--radio-border-color, #c8c9cc);
  font-size: 0.85333rem;
  font-size: var(--radio-size, 0.85333rem);
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
  -webkit-transition-duration: var(--radio-transition-duration, 0.2s);
          transition-duration: var(--radio-transition-duration, 0.2s);
}
.van-radio__icon--round {
  border-radius: 100%;
}
.van-radio__icon--checked {
  color: #fff;
  color: var(--white, #fff);
  background-color: #1989fa;
  background-color: var(--radio-checked-icon-color, #1989fa);
  border-color: #1989fa;
  border-color: var(--radio-checked-icon-color, #1989fa);
}
.van-radio__icon--disabled {
  background-color: #ebedf0;
  background-color: var(--radio-disabled-background-color, #ebedf0);
  border-color: #c8c9cc;
  border-color: var(--radio-disabled-icon-color, #c8c9cc);
}
.van-radio__icon--disabled.van-radio__icon--checked {
  color: #c8c9cc;
  color: var(--radio-disabled-icon-color, #c8c9cc);
}
.van-radio__label {
  word-wrap: break-word;
  padding-left: 0.42667rem;
  padding-left: var(--radio-label-margin, 0.42667rem);
  color: #323233;
  color: var(--radio-label-color, #323233);
  line-height: 0.85333rem;
  line-height: var(--radio-size, 0.85333rem);
}
.van-radio__label--left {
  float: left;
  margin: 0 0.42667rem 0 0;
  margin: 0 var(--radio-label-margin, 0.42667rem) 0 0;
}
.van-radio__label--disabled {
  color: #c8c9cc;
  color: var(--radio-disabled-label-color, #c8c9cc);
}
.van-radio__label:empty {
  margin: 0;
}
/* stylelint-disable selector-type-no-unknown, selector-max-type */
/**
 * Entry of basic styles
 */
.van-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.van-multi-ellipsis--l2 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-multi-ellipsis--l3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.van-hairline,
.van-hairline--top,
.van-hairline--left,
.van-hairline--right,
.van-hairline--bottom,
.van-hairline--top-bottom,
.van-hairline--surround {
  position: relative;
}
.van-hairline::after,
.van-hairline--top::after,
.van-hairline--left::after,
.van-hairline--right::after,
.van-hairline--bottom::after,
.van-hairline--top-bottom::after,
.van-hairline--surround::after {
  position: absolute;
  box-sizing: border-box;
  -webkit-transform-origin: center;
          transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.van-hairline--top::after {
  /* prettier-ignore */
  border-top-width: 1PX;
}
.van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX;
}
.van-hairline--right::after {
  /* prettier-ignore */
  border-right-width: 1PX;
}
.van-hairline--bottom::after {
  /* prettier-ignore */
  border-bottom-width: 1PX;
}
.van-hairline--top-bottom::after {
  /* prettier-ignore */
  border-width: 1PX 0;
}
.van-hairline--surround::after {
  /* prettier-ignore */
  border-width: 1PX;
}
html,
body,
page {
  font-size: 0.59733rem;
