.van-switch {
  position: relative;
  display: inline-block;
  box-sizing: content-box;
  width: 2em;
  width: var(--switch-width, 2em);
  height: 1em;
  height: var(--switch-height, 1em);
  background-color: #fff;
  background-color: var(--switch-background-color, #fff);
  border: 0.04267rem solid rgba(0, 0, 0, 0.1);
  border: var(--switch-border, 0.04267rem solid rgba(0, 0, 0, 0.1));
  border-radius: 1em;
  border-radius: var(--switch-node-size, 1em);
  -webkit-transition: background-color 0.3s;
  transition: background-color 0.3s;
  -webkit-transition: background-color var(--switch-transition-duration, 0.3s);
  transition: background-color var(--switch-transition-duration, 0.3s);
}
.van-switch__node {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 100%;
  z-index: 1;
  z-index: var(--switch-node-z-index, 1);
  width: 1em;
  width: var(--switch-node-size, 1em);
  height: 1em;
  height: var(--switch-node-size, 1em);
  background-color: #fff;
  background-color: var(--switch-node-background-color, #fff);
  box-shadow: 0 0.128rem 0.04267rem 0 rgba(0, 0, 0, 0.05), 0 0.08533rem 0.08533rem 0 rgba(0, 0, 0, 0.1), 0 0.128rem 0.128rem 0 rgba(0, 0, 0, 0.05);
  box-shadow: var(--switch-node-box-shadow, 0 0.128rem 0.04267rem 0 rgba(0, 0, 0, 0.05), 0 0.08533rem 0.08533rem 0 rgba(0, 0, 0, 0.1), 0 0.128rem 0.128rem 0 rgba(0, 0, 0, 0.05));
  -webkit-transition: -webkit-transform 0.3s cubic-bezier(.3, 1.05, .4, 1.05);
  transition: -webkit-transform 0.3s cubic-bezier(.3, 1.05, .4, 1.05);
  transition: transform 0.3s cubic-bezier(.3, 1.05, .4, 1.05);
  transition: transform 0.3s cubic-bezier(.3, 1.05, .4, 1.05), -webkit-transform 0.3s cubic-bezier(.3, 1.05, .4, 1.05);
  -webkit-transition: -webkit-transform var(--switch-transition-duration, 0.3s) cubic-bezier(.3, 1.05, .4, 1.05);
  transition: -webkit-transform var(--switch-transition-duration, 0.3s) cubic-bezier(.3, 1.05, .4, 1.05);
  transition: transform var(--switch-transition-duration, 0.3s) cubic-bezier(.3, 1.05, .4, 1.05);
  transition: transform var(--switch-transition-duration, 0.3s) cubic-bezier(.3, 1.05, .4, 1.05), -webkit-transform var(--switch-transition-duration, 0.3s) cubic-bezier(.3, 1.05, .4, 1.05);
}
.van-switch__loading {
  position: absolute !important;
  top: 25%;
  left: 25%;
  width: 50%;
  height: 50%;
}
.van-switch--on {
  background-color: #1989fa;
  background-color: var(--switch-on-background-color, #1989fa);
}
.van-switch--on .van-switch__node {
  -webkit-transform: translateX(calc(2em - 1em));
          transform: translateX(calc(2em - 1em));
  -webkit-transform: translateX(calc(var(--switch-width, 2em) - var(--switch-node-size, 1em)));
          transform: translateX(calc(var(--switch-width, 2em) - var(--switch-node-size, 1em)));
}
.van-switch--disabled {
  opacity: 0.4;
  opacity: var(--switch-disabled-opacity, 0.4);
}
/* stylelint-disable selector-type-no-unknown, selector-max-type */
/**
 * Entry of basic styles
 */
.van-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.van-multi-ellipsis--l2 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-multi-ellipsis--l3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.van-hairline,
.van-hairline--top,
.van-hairline--left,
.van-hairline--right,
.van-hairline--bottom,
.van-hairline--top-bottom,
.van-hairline--surround {
  position: relative;
}
.van-hairline::after,
.van-hairline--top::after,
.van-hairline--left::after,
.van-hairline--right::after,
.van-hairline--bottom::after,
.van-hairline--top-bottom::after,
.van-hairline--surround::after {
  position: absolute;
  box-sizing: border-box;
  -webkit-transform-origin: center;
          transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.van-hairline--top::after {
  /* prettier-ignore */
  border-top-width: 1PX;
}
.van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX;
}
.van-hairline--right::after {
  /* prettier-ignore */
  border-right-width: 1PX;
}
.van-hairline--bottom::after {
  /* prettier-ignore */
  border-bottom-width: 1PX;
}
.van-hairline--top-bottom::after {
  /* prettier-ignore */
  border-width: 1PX 0;
}
.van-hairline--surround::after {
  /* prettier-ignore */
  border-width: 1PX;
}
html,
body,
page {
  font-size: 0.59733rem;
}
